/*
CSS file used by the Application

It provides placement and style information for the components in the app
and only exists for aesthetic purposes. The app would work just fine if this
file was empty.
*/

input {
    margin: 5px;
}

body {
    background-color: #272629;
    color: white;
    font-family: "Segoe UI Light", sans-serif;
}

#wrapper {
    margin-left: auto;
    margin-right: auto;
    margin-top: 100px;
    width: 960px;
}

canvas {
    margin: 0 auto;
}

textarea {
    resize: none;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
    border-style: none;
    border-radius: 10px;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    outline: none;
}

textarea:empty {
    background-color: #424045;
}

textarea:focus {
    background-color: white;
}

input[type=number] {
    font-family: "Segoe UI Light", sans-serif;
    font-size: 20px;
    line-height: 20px;
    margin: 10px;
    padding: 10px;
    width: 50px;
    background-color: #424045;
    border-style: none;
    border-radius: 10px;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    color: white;
}

input[type=number]:focus:in-range {
    outline: none;
    background-color: white;
    color: #424045;
}

input[type=number]:out-of-range {
    background-color: indianred;
}

#bodyText {
    font-size: 20px;
}
#grid-size-picker {
    text-align: left;
    padding-bottom: 20px;
}

#grid-size-message {
    color: indianred;
}

#grid {
    text-align: center;
    display: inline-block;
    float: left;
}

#edge-list {
    display: inline-block;
    width: 350px;
    margin-top: 10%;
    padding-left: 30px;
}

#edge-list button {
    font-family: "Segoe UI Light", sans-serif;
    font-size: 1em;
    margin-right: 10px;
    padding: 10px 20px;
    background-color: #424045;
    border-style: none;
    border-radius: 10px;
    -webkit-transition: 0.5s;
    transition: 0.3s;
    color: white;
}

#edge-list button:hover {
    background-color: #03DAC6;
}

#edge-list button:active{
    background-color: #014b44;
}

button:focus {
    outline: none;
}

#app-title {
    margin-bottom: 30px;
    font-size: 60px;
}
